.container{
    max-width: 65rem;
    padding: 0 2rem;
    margin: 0 auto;
    display: flex;
    position: relative;
    background: #1E441F;
}


.nav-btn{
    display: flex;
}


.nav-links > ul{
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-link{
    position: relative;
}

.nav-link > .Link{
    line-height: 3rem;
    color: #fff;
    padding: 0 .8rem;
    letter-spacing: 1px;
    font-size: .95rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: .5s;
}

.nav-link > .Link > i{
    margin-left: .2rem;
}

.nav-link:hover > .Link{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.dropdown{
    position: absolute;
    top: 100%;
    left: 0;
    width: 12rem;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
    opacity: 0;
    pointer-events: none;
    transition: .5s;
    /* box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px; */
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.dropdown ul{
    position: relative;
}

.dropdown-link > .Link{
    display: flex;
    background-color: #fff;
    color: #1E441F;
    padding: .5rem 1rem;
    font-size: .9rem;
    align-items: center;
    justify-content: space-between;
    transition: .3s;
}

.dropdown-link:hover > .Link{
    background-color: #1E441F;
    color: #fff;
}

.dropdown-link:not(:nth-last-child(2)){
    border-bottom: 1px solid #efefef;
}

.dropdown-link i{
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.arrow{
    position: absolute;
    width: 11px;
    height: 11px;
    top: -5.5px;
    left: 32px;
    background-color: #fff;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    cursor: pointer;
    transition: .3s;
    z-index: -1;
}

.dropdown-link:first-child:hover ~ .arrow{
    background-color: #1E441F;
}

.dropdown-link{
    position: relative;
}


.nav-link:hover > .dropdown,
.dropdown-link:hover > .dropdown{
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 1;
    pointer-events: auto;
}

.hamburger-menu-container{
    flex: 1 1;
    display: none;
    align-items: center;
    justify-content: flex-end;
    padding: 5px 0;
}
.hamburger-college-name{
    flex: 2 1;
    display: flex;
    align-items: center;
}
.hamburger-college-name h3{
    color: #fff;
    letter-spacing: 3px;
    margin-left: 10px;
    font-size: 25px;
}
.hamburger-menu{
    flex: 1 1;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.hamburger-menu div{
    width: 1.6rem;
    height: 3px;
    border-radius: 3px;
    background-color: #fff;
    position: relative;
    z-index: 1001;
    transition: .5s;
}

.hamburger-menu div:before,
.hamburger-menu div:after{
    content: '';
    position: absolute;
    width: inherit;
    height: inherit;
    background-color: #fff;
    border-radius: 3px;
    transition: .5s;
}

.hamburger-menu div:before{
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
}

.hamburger-menu div:after{
    -webkit-transform: translateY(7px);
            transform: translateY(7px);
}

#check{
    position: absolute;
    top: 50%;
    right: 1.5rem;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 2.5rem;
    height: 2.5rem;
    z-index: 90000;
    cursor: pointer;
    opacity: 0;
    display: none;
}

#check:checked ~ .hamburger-menu-container .hamburger-menu div{
    background-color: transparent;
}

#check:checked ~ .hamburger-menu-container .hamburger-menu div:before{
    -webkit-transform: translateY(0) rotate(-45deg);
            transform: translateY(0) rotate(-45deg);
}

#check:checked ~ .hamburger-menu-container .hamburger-menu div:after{
    -webkit-transform: translateY(0) rotate(45deg);
            transform: translateY(0) rotate(45deg);
}

@-webkit-keyframes animation{
    from{
        opacity: 0;
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@keyframes animation{
    from{
        opacity: 0;
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }
    to{
        opacity: 1;
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

@media (max-width: 920px){
    .hamburger-menu-container{
        display: flex;
    }

    #check{
        display: block;
    }
    header{
        padding: 0;
    }
    .top-container{
        display: none;
    }

    .nav-btn{
        position: fixed;
        height: calc(100vh - 3rem);
        top: 3.6rem;
        left: 0;
        width: 100%;
        background-color: #3f6240;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        overflow-x: hidden;
        overflow-y: auto;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: .65s;
    }

    #check:checked ~ .nav-btn{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    .nav-links{
        flex: initial;
        width: 100%;
    }

    .nav-links > ul{
        flex-direction: column;
    }

    .nav-link{
        width: 100%;
        opacity: 1;
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }

    .nav-link > .Link{
        line-height: 1;
        padding: 1rem 2rem;
        color: #fff;
    }

    .nav-link:hover > .Link{
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: #1E441F;
    }

    .dropdown{
        position: static;
        position: initial;
        top: auto;
        top: initial;
        left: auto;
        left: initial;
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        opacity: 1;
        pointer-events: auto;
        width: 100%;
        padding: 0;
        background-color: #2d4b2e;
        display: none;
    }
    
    .nav-link:hover > .dropdown,
    .dropdown-link:hover > .dropdown{
        display: block;
    }

    .nav-link:hover > .Link > i,
    .dropdown-link:hover > a > i{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }

    .dropdown-link > .Link{
        background-color: transparent;
        color: #fff;
        padding: 1.2rem 2rem;
        line-height: 1;
        margin-left: 4%;
    }

    .dropdown-link:not(:nth-last-child(2)){
        border-bottom: none;
    }

    .arrow{
        z-index: 1;
        background-color: #1E441F;
        left: 14%;
        -webkit-transform: scale(1.1) rotate(45deg);
                transform: scale(1.1) rotate(45deg);
        transition: .5s;
    }

    .nav-link:hover .arrow{
        background-color: #1E441F;
    }

    .dropdown .dropdown .arrow{
        display: none;
    }

    .dropdown-link:hover > .Link{
        background-color: #285229;
    }

    .dropdown-link:first-child:hover ~ .arrow{
        background-color: #1E441F;
    }

    .nav-link > .Link > i{
        font-size: 1.1rem;
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg);
        transition: .7s;
    }

    .dropdown i{
        font-size: 1rem;
        transition: .7s;
    }

}
