}
.marquee:before,
.marquee:after {
  position: absolute;
  top: 0;
  width: 50px;
  height: 30px;
  content: "";
  z-index: 1;
}
.marquee__content {
  width: 100%;
  display: flex;
  line-height: 30px;
  -webkit-animation: marquee 20s linear infinite forwards;
          animation: marquee 20s linear infinite forwards;
}
.marquee__content:hover {
